.value {
    background-color: darkslateblue;
    color: white;

    text-align: center;
    font-size: 50px;
}

.valueTxt {
    background-color: darkslateblue;
    color: white;

    text-align: center;
    font-size: 25px;
}

.error {
    background-color: darkslateblue;
    color: red;

    text-align: center;
    font-size: 50px;
}

.errorTxt {
    background-color: darkslateblue;
    color: red;
    margin-top: 30px;
    text-align: center;
    font-size: 30px;
}

.inputBlock {
    display: flex;
    flex-direction: column;
    border: 3px solid white;
    border-radius: 7px;
    width: 260px;
    height: 85px;
    text-align: center;
    margin: 15px 15px 0 15px;
    align-items: center;
    justify-content: center;
}

.button {
    border-radius: 7px;
    width: 65px;
    height: 35px;
    text-align: center;
    font-size: 20px;
    margin: 8px 11px 15px;
}

.buttonBlock {
    width: 260px;
    height: 50px;
    border: 3px solid white;
    border-radius: 7px;
    margin: 15px 15px 15px 15px;
}

.counter {
    display: flex;
    flex-direction: column;
    border: 3px solid white;
    border-radius: 11px;
    width: 296px;
    height: 190px;
    margin: 0 10px 15px 10px;
    align-items: center;
    justify-content: center;
    background-color: darkslateblue;
}